                        },
                    },
                    {
                            astype: "TextField",
                            label: this.$t('LocatorStartDate'), 
                            name: "LocatorStartDate",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('UtilityAbbrev'),
                            name: "UtilityAbbrev",
                            value: "",
                            placeholder: "",
                            validationRules: {
