                            },
                            disabled: true,
                        },
                        {
                        astype: "NumericField",
                        label: this.$t('FeetLocated'),
                        name: "FeetLocated",
                        visibility: true,
                        value: "",
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                            astype: "TextField",
                            label: this.$t('CallDate'),
                            name: "CallDate",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LocatorName'),
                            name: "LocatorName",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ParseDate'),
                            name: "ParseDate",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Authorized'),
                            name: "Authorized",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('CallerName'),
                            name: "CallerName",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                         {
                            astype: "SlotField",
                            label: this.$t('TicketImage'),
                            name: "TicketImage",
                            value: "",
                            visibility: true,
                            validationRules: {
                                "required": false
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('CallerPhone'),
                            name: "CallerPhone",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                         {
                            astype: "TextField",
                            label: this.$t('StreetName'),
                            name: "StreetName",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                            disabled: true,
                        },
                         {
                            astype: "TextAreaField",
                            label: this.$t('FlagReason'),
                            name: "FlagReason",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            config: {
                                rows: 4
                            },
                            validationRules: { "required": false, max: 2000 },
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
