                ],
            }, ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save and Process",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            FieldOptions: [],
            DataType: 'nvarchar',
            SelectedValue: '',
            IsCDCCode: false,
            additionalId: '',
            TermCode: '',
            TicketNumber: '',
            TransmissionId: '',
            IsViewTicket: false,
            TicketContent: '',
            UtilityId: '',
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetTicketTypes();
        await vm.GetStatusCodes();
        await vm.LoadAdditionalInfoByClientId();
        await vm.GetExceptionRecord();
        await vm.GetSbaIdListByUtilityId();
    },
    methods: {
        GetExceptionRecord: function () {
            var vm = this;
            vm.isLoading = true;
            let selctedValue = [];
            let selctedDistrict = [];
            let selctedSBAList = [];
            let SelectedClientList=[];
            var url = `ClientId=${vm.ClientId}&ColumnId=${vm.ColumnIds}&requestFrom=${vm.requestFrom}`;
            locateBillingService.GetExceptionRecord(url).then(response => {
                if (response.data.data) {
                    var item= response.data.data;
                    vm.additionalId=response.data.data.CLIENT_ADDITIONAL_ID;
                    var InvoiceGroupingList= item.InvoiceGroupingList;
                    if (InvoiceGroupingList) {
                    var json = InvoiceGroupingList;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.DIVISIONS_ID    
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("InvoiceGrouping", "config.options", selctedValue);
                    }
                    }
                    var CostCenterList= item.CostCenterList;
                    if (CostCenterList) {
                    var jsonVal = CostCenterList;
                    if (jsonVal != null) {
                        jsonVal.forEach(item => {
                            let obj = {
                                name: item.TEXT,
                                value: item.VALUE    
                            };
                            selctedDistrict.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("DistrictCode", "config.options", selctedDistrict);
                    }
                    }
                    var SBAList= item.GetSBAidsDropdownData;
                    if (SBAList) {
                    var jsondata = SBAList;
                    if (jsondata != null) {
                        jsondata.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.SBA_ID    
                            };
                            selctedSBAList.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("SBAId", "config.options", selctedSBAList);
                    }
                    }
                    var ClientList= item.GroupedSelectList;
                    if (ClientList) {
                    var jsondataval = ClientList;
                    if (jsondataval != null) {
                        jsondataval.forEach(item => {
                            let obj = {
                                description: null,
                                displayValue: item.CHILD_NAME,
                                groupText: item.PARENT_NAME,
                                groupValue: null,
                                metaData: null,
                                text: item.CHILD_NAME,
                                value: item.CHILD_USER_ID   
                            };
                            SelectedClientList.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("ContractName", "config.options", SelectedClientList);
                    }
                    }

                    if (vm.isProcessed == 1) {
                        vm.$refs.dynamicform.updateProperty("FlagReason", "visibility", true);
                    }else{
                        vm.$refs.dynamicform.updateProperty("FlagReason", "visibility", false);
                    }
                    vm.$refs.dynamicform.UpdateKeyValue();
                    vm.TicketNumber=item.TICKETNUMBER;
                    vm.TransmissionId= item.TRANSMISSION_ID;
                    vm.UtilityId= item.CLIENT_ADDITIONAL_ID;
                    vm.$refs.dynamicform.updateProperty("TicketNumber", "value", item.TICKETNUMBER);
                    vm.$refs.dynamicform.updateProperty("State", "value", item.STATE);
                    vm.$refs.dynamicform.updateProperty("TermCode", "value", item.TERM_CODE);
                    vm.$refs.dynamicform.updateProperty("FeedName", "value", item.FEEDNAME);
                    vm.$refs.dynamicform.updateProperty("WireCenter", "value", item.WIRE_CENTER); 
                    vm.$refs.dynamicform.updateProperty("County", "value", item.COUNTY);
                    vm.$refs.dynamicform.updateProperty("TicketType", "value", item.TICKETTYPE);
                    vm.$refs.dynamicform.updateProperty("City", "value", item.CITY);
                    vm.$refs.dynamicform.updateProperty("StatusCode", "value", item.STATUS_CODE);
                    vm.$refs.dynamicform.updateProperty("ContractName", "value", item.CLIENT_ID);
                    vm.$refs.dynamicform.updateProperty("Description", "value", item.DESCRIPTION);
                    vm.$refs.dynamicform.updateProperty("Excavator", "value", item.EXCAVATOR);
                    vm.$refs.dynamicform.updateProperty("WorkDoneFor", "value", item.WORK_DONE_FOR);
                    vm.$refs.dynamicform.updateProperty("UtilityName", "value", item.UTILITY_NAME);
                    vm.$refs.dynamicform.updateProperty("TypeOfWork", "value", item.TYPEOFWORK);
                    vm.$refs.dynamicform.updateProperty("UtilityId", "value", item.CLIENT_ADDITIONAL_ID);
                    vm.$refs.dynamicform.updateProperty("LocatorStartDate", "value", vm.$options.filters.formatDateTime(item.INITIAL_STATUS_DATE));
                    vm.$refs.dynamicform.updateProperty("UtilityAbbrev", "value", item.UTILITY_ABBREV);
                    vm.$refs.dynamicform.updateProperty("LocatorEndDate", "value", vm.$options.filters.formatDateTime(item.CLOSED_DATE));
                    vm.$refs.dynamicform.updateProperty("SBAId", "value", item.BE_SBA_ID);
                    vm.$refs.dynamicform.updateProperty("TicketStartDate", "value", vm.$options.filters.formatDateTime(item.WORK_DATE));
                    vm.$refs.dynamicform.updateProperty("InvoiceGrouping", "value", item.DIVISION_ID);
                    vm.$refs.dynamicform.updateProperty("MinutesWorked", "value", item.MINUTES_WORKED);
                    vm.$refs.dynamicform.updateProperty("TransmitDate", "value", vm.$options.filters.formatDateTime(item.TRANSMIT_DATE));
                    vm.$refs.dynamicform.updateProperty("FeetLocated", "value", item.FEET_LOCATED);
                    vm.$refs.dynamicform.updateProperty("CallDate", "value", vm.$options.filters.formatDateTime(item.CALL_DATE));
                    vm.$refs.dynamicform.updateProperty("LocatorName", "value", item.LOCATOR_NAME);
                    vm.$refs.dynamicform.updateProperty("ParseDate", "value", vm.$options.filters.formatDateTime(item.PARSE_DATE));
                    vm.$refs.dynamicform.updateProperty("Authorized", "value", item.AUTHORIZED);
                    vm.$refs.dynamicform.updateProperty("CallerName", "value", item.CALLER_NAME);
                    vm.$refs.dynamicform.updateProperty("TicketImage", "value", item.TICKETIMAGE);
                    vm.$refs.dynamicform.updateProperty("CallerPhone", "value", item.CALLER_PHONE);
                    vm.$refs.dynamicform.updateProperty("StreetName", "value", item.STREETNAME);
                    vm.$refs.dynamicform.updateProperty("Comment", "value", item.REMARKS);
                    vm.$refs.dynamicform.updateProperty("DistrictCode", "value", item.DISTRICT_CODE);
                    vm.$refs.dynamicform.UpdateKeyValue();

                    // Set old values for change tracking
                    vm.oldValues = {
                        TicketNumber: item.TICKETNUMBER,
                        State: item.STATE,
                        TermCode: item.TERM_CODE,
                        FeedName: item.FEEDNAME,
                        WireCenter: item.WIRE_CENTER,
                        County: item.COUNTY,
                        TicketType: item.TICKETTYPE,
                        City: item.CITY,
                        StatusCode: item.STATUS_CODE,
                        ContractName: item.CLIENT_ID,
                        Description: item.DESCRIPTION,
                        Excavator: item.EXCAVATOR,
                        WorkDoneFor: item.WORK_DONE_FOR,
                        UtilityName: item.UTILITY_NAME,
                        TypeOfWork: item.TYPEOFWORK,
                        UtilityId: "",
                        LocatorStartDate: vm.$options.filters.formatDateTime(item.INITIAL_STATUS_DATE),
                        UtilityAbbrev: item.UTILITY_ABBREV,
                        LocatorEndDate: vm.$options.filters.formatDateTime(item.CLOSED_DATE),
                        SBAId: item.SBA_ID,
                        TicketStartDate: vm.$options.filters.formatDateTime(item.WORK_DATE),
                        InvoiceGrouping: item.DIVISION_ID,
                        MinutesWorked: item.MINUTES_WORKED,
                        TransmitDate: vm.$options.filters.formatDateTime(item.TRANSMIT_DATE),
                        FeetLocated: item.FEET_LOCATED,
                        CallDate: vm.$options.filters.formatDateTime(item.CALL_DATE),
                        LocatorName: item.LOCATOR_NAME,
                        ParseDate: vm.$options.filters.formatDateTime(item.PARSE_DATE),
                        Authorized: item.AUTHORIZED,
                        CallerName: item.CALLER_NAME,
                        TicketImage: item.TICKETIMAGE,
                        CallerPhone: item.CALLER_PHONE,
                        StreetName: item.STREETNAME,
                        Comment: item.REMARKS,
                        DistrictCode: item.DISTRICT_CODE
                    };

               }
               vm.isLoading = false;
           }).catch(() => {
               vm.isLoading = false;
           });
        },
        onSelectionChange(value,x) {
            // Ensure value is an array for multiple mode
            var vm = this;
            vm.isLoading = true;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                  let SelectedUtilityList=[];
                  if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.UTILITY_NAME,
                                value: item.UTILITY_ID
                            };
                            SelectedUtilityList.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("UtilityId", "config.options", SelectedUtilityList);
                        vm.$refs.dynamicform.UpdateKeyValue();
                        vm.UtilityId = vm.$refs.dynamicform.getPropertyValue("UtilityId", "value");
                        vm.GetSbaIdListByUtilityId();
                    }
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.SelectedUtilityList = []; 
                vm.$refs.dynamicform.updateProperty("UtilityId", "config.options", SelectedUtilityList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        onSelectionUtilityChange(value,x) {
            var vm = this;
            vm.UtilityId = x.value;
            vm.GetSbaIdListByUtilityId();
        },
         CloseTemplate: function () {
            var vueObj = this;
            vueObj.IsViewTicket = false;
        },
         viewTicketImage: async function() {
            var vm =this;
            vm.IsViewTicket= !vm.IsViewTicket;
            var url = `TicketNumber=${vm.TicketNumber}&TransmissionId=${vm.TransmissionId}`
             await locateBillingService.ViewTicketImage(url).then((response) => {
                if (response.data.length>0) {
                    vm.TicketContent = response.data[0].RAW_IMAGE;
                }
            });
        },
        ViewCDCCode: async function (e, field) {
            var vm = this;
            vm.TermCode = vm.$refs.dynamicform.getPropertyValue("TermCode", "value");
            vm.IsCDCCode = !vm.IsCDCCode;

        },
         GetTicketTypes: async function () {
            var vm = this;
            let selctedValue = [];
            locateBillingService.GetTicketTypes().then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.TYPE_NAME,
                                value: item.TYPE_ID
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("TicketType", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
           GetStatusCodes: async function () {
            var vm = this;
            let selctedValue = [];
            locateBillingService.GetStatusCodes().then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.TEXT,
                                value: item.VALUE 
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("StatusCode", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
         LoadAdditionalInfoByClientId: async function () {
            var vm = this;
            let selctedValue = [];
             var url = `ClientId=${vm.ClientId}`;
            locateBillingService.LoadAdditionalInfoByClientId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.UTILITY_NAME,
                                value: item.UTILITY_ID
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("UtilityId", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
         GetSbaIdListByUtilityId: async function () {
            var vm = this;
            let selctedValue = [];
            var url = `UtilityId=${vm.UtilityId}`;
            locateBillingService.GetSbaIdListByUtilityId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.VALUE
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("SBAId", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
        Close: function () {
            this.$parent.IsEditException = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            vm.UpdateChangedValues(formfields);
            var ProcessLogJson = JSON.stringify(vm.MakeJsonForProcessLog(formfields));
            var postJSON = {
                columnId: vm.ColumnIds,
                MinutesWorked: formfields.MinutesWorked,
                TicketType: formfields.TicketType,
                Status: formfields.StatusCode,
                SbaId: formfields.SBAId,
                FeetLocated: formfields.FeetLocated,
                Authorized: formfields.Authorized,
                Comment: formfields.Comment,
                newStringifyModel: JSON.stringify(formfields),
                oldStringifyModel: JSON.stringify(vm.oldValues),
                FlagReason: formfields.FlagReason,
                ClientIds: formfields.ContractName,
                ClientAdditionalId: formfields.UtilityId,
                ProcessLogJson: ProcessLogJson,
                costCenter: formfields.DistrictCode,
                WireCenter: formfields.WireCenter,
                finalStatus: formfields.StatusCode,
                EditRequestFrom: vm.EditRequestFrom,
                DivisionId: formfields.InvoiceGrouping,
                TermCode: formfields.TermCode,
                UtilityName: formfields.UtilityName,
                ClientAdditionalIdStr: formfields.UtilityId,
                UtilityAbbrev: formfields.UtilityAbbrev,
                ReqFrom: 'flag'
            };
            locateBillingService.UpdateException(postJSON).then(function (response) {
                vm.isLoading = false;
                if (response.data) {
                      vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                      if(vm.requestFrom=="unprocess"){
                            vm.$parent.FetchUnprocessedRecords();
                      }else{
                            vm.$parent.FetchFlagRecords();
                      }
                      vm.Close();
                } else {
                    if (response.data.invalidKey) {
                        response.data.invalidKey.forEach(function (ctr) {
                            vm.$refs.dynamicform.showError(ctr.ctrId, ctr.ctrMsg);
                        });
                    }
                    vm.ShowAlert(vm.$t("CorrectHighlightedErrors"), "warning", true, vm.$t("Alert"));
                }
            }).catch(function () {
                vm.isLoading = false;
                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t("Alert"));
            });
        },
        UpdateChangedValues(formfields) {
            var data = "{";
            for (var key in formfields) {
                if (formfields[key] !== this.oldValues[key]) {
                    data += '"' + key + '":' + JSON.stringify(formfields[key]) + ",";
                }
            }
            if (data === "{") {
                this.$refs.dynamicform.updateProperty("Comment", "value", "");
            } else {
                data = data.substr(0, data.length - 1) + "}";
                this.$refs.dynamicform.updateProperty("Comment", "value", data);
            }
        },
        MakeJsonForProcessLog(formfields) {
            var dataArray = [];
            for (var key in formfields) {
                if (formfields[key] !== this.oldValues[key]) {
                    dataArray.push({
                        ControlName: key,
                        OldData: this.oldValues[key] || "",
                        NewData: formfields[key] || ""
                    });
                }
            }
            return dataArray;
        }
    }
}
</script>
